:root {
    --bg-color: #042B15;
    --bg2: #0F4127;
    --green1: #059733;
    --gradient1: #138E4E;
    --gradient2: #039830;
    --text: #00DF94;

}
* {
    margin: 0;padding: 0;
    border: none;outline: none;
    font-family: nunito;
    box-sizing: border-box;
    color: var(--text);
    /* overflow-x: hidden; */
    transition: all .4s ease-in;
}

body {
    background: var(--bg-color) ;
    margin: 20px 60px;
}
div.app {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr auto 1fr;
    /* gap: 10px; */
    align-items: flex-start;
    height: 90vh;
}
div.header, div.footer {
    background: var(--bg2);
    border-radius: 70px;
    padding: 15px 0;
    text-align: center;
}
div.header {
    font-size: 1.5rem;
    text-transform: uppercase;
    color: #fff;
}
div.footer {
    font-size: .7rem;
    text-transform: uppercase;
    cursor: pointer;
    align-self:flex-end;
}
div.footer:hover {
    background: #0a6e3a;
}
div.footer h1  {
    color: #fff;
    font-weight: 600;
}

div.main > .sections{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    justify-items: center;
}

div.main > .sections > div{
    background: var(--bg2);
    padding: 50px 30px;
    border-radius: 35px;
    text-transform: capitalize;
    width: 30%;
}

div.main > .sections > div > p{
    margin-bottom: 15px;
    font-size: 1rem;
    font-weight: bolder;
}